body {
  font-family: 'Roboto', sans-serif;
  color: var(--title-color);
  background: var(--main-site-background);
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: currentColor;
}

.logo{
  padding: 0px 0px 10px 0px;
}

.conteiner{
  padding: 10px 10px 10px 10px;
}

.main-menu{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.main-title{
  color:blue
}

.container {
  width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

/* ------------------- header ----------------------- */

.header {
  border-bottom: 1px solid #ececec;
}

.header__container {
  /* @include flex(center, null, null, wrap); */
  display: flex;
  align-items: center;
}

.menu {
  display: flex;
  align-items: center;
}

.menu__list {
  display: flex;
  margin-left: 93px;
}

.menu__link {
  display: block;
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  padding-top: 32px;
  padding-bottom: 32px;
  transition: color var(--transition-duration) var(--timig-function),
    opacity var(--transition-duration) var(--timig-function);
}

  .menu__link::after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background-color: var(--accent-color);
    left: 0;
    bottom: 0;
    opacity: 0;
    transition: opacity var(--transition-duration) var(--timig-function);
  } 

  .menu__link:focus:after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background-color: var(--accent-color);
    left: 0;
    bottom: 0;
    opacity: 1;
    transition: opacity var(--transition-duration) var(--timig-function);
  }

  .menu__link:hover::after {
    opacity: 1;
  }


.header__list {
  display: flex;
  margin-left: auto;
}

.header__mail,
.header__tel {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  color: var(--main-text-color);
  display: flex;
  align-items: center;
  padding-top: 32px;
  padding-bottom: 32px;
  transition: color var(--transition-duration) var(--timig-function);
}

.header .link:hover {
  color: var(--accent-color);
}

.header .link:focus {
  color: var(--accent-color);
  outline: 1px solid var(--accent-color);
}

.header__icon--envelope,
.header__icon--smartphone {
  margin-right: 10px;
  fill: currentColor;
}

.active {
  color: var(--accent-color);
}
.active::after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background-color: var(--accent-color);
    left: 0;
    bottom: 0;
    opacity: 1;
  }


.item--tab {
  margin-right: 50px;
}



/* ---------------------- logo ---------------------- */

.logo {
  font-family: 'Raleway', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  color: var(--accent-color);
  padding-top: 24px;
  padding-bottom: 25px;
}

  .logo__span--header {
    color: var(--header-logo-color);
  }

    .logo__span--footer {
    color: var(--footer-logo-color);
  }


.footer__logo {
  display: block;
  padding-top: 0;
  padding-bottom: 0px;
  margin-bottom: 20px;
}