h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.address {
  font-style: normal;
}

img {
  display: block;
}

body {
  color: var(--title-color);
  background: var(--main-site-background);
  font-family: Roboto, sans-serif;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

.logo {
  padding: 0 0 10px;
}

.conteiner {
  padding: 10px;
}

.main-menu {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.main-title {
  color: #00f;
}

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  border-bottom: 1px solid #ececec;
}

.header__container, .menu {
  align-items: center;
  display: flex;
}

.menu__list {
  margin-left: 93px;
  display: flex;
}

.menu__link {
  letter-spacing: .02em;
  transition: color var(--transition-duration) var(--timig-function), opacity var(--transition-duration) var(--timig-function);
  padding-top: 32px;
  padding-bottom: 32px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  display: block;
  position: relative;
}

.menu__link:after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: var(--accent-color);
  opacity: 0;
  transition: opacity var(--transition-duration) var(--timig-function);
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu__link:focus:after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: var(--accent-color);
  opacity: 1;
  transition: opacity var(--transition-duration) var(--timig-function);
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu__link:hover:after {
  opacity: 1;
}

.header__list {
  margin-left: auto;
  display: flex;
}

.header__mail, .header__tel {
  letter-spacing: .02em;
  color: var(--main-text-color);
  transition: color var(--transition-duration) var(--timig-function);
  align-items: center;
  padding-top: 32px;
  padding-bottom: 32px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  display: flex;
}

.header .link:hover {
  color: var(--accent-color);
}

.header .link:focus {
  color: var(--accent-color);
  outline: 1px solid var(--accent-color);
}

.header__icon--envelope, .header__icon--smartphone {
  fill: currentColor;
  margin-right: 10px;
}

.active {
  color: var(--accent-color);
}

.active:after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: var(--accent-color);
  opacity: 1;
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.item--tab {
  margin-right: 50px;
}

.logo {
  letter-spacing: .03em;
  color: var(--accent-color);
  padding-top: 24px;
  padding-bottom: 25px;
  font-family: Raleway, sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.19;
}

.logo__span--header {
  color: var(--header-logo-color);
}

.logo__span--footer {
  color: var(--footer-logo-color);
}

.footer__logo {
  margin-bottom: 20px;
  padding-top: 0;
  padding-bottom: 0;
  display: block;
}

